<template>
  <div class="conjuncture">
    <appHead mid="" right="true" color="white"></appHead>
    <div class="wrap">
      <div class="market">
        <div class="conjuncture1 tag active" @click="$router.push('/conjuncture')"></div>
        <div class="deal tag" @click="$router.push('/buySell')"></div>
        <div class="entrust tag" @click="$router.push('/entrust')"></div>
      </div>
      <div class="conjuncture_detail">
        <div class="data_wrap">
          <div class="coin_type">
            <span>BNB/BTC</span>
            <img class="rotate" src="../../assets/image/up_down.png" alt="">
            <ul class="tab">
              <li>LTB/BTC</li>
              <li>BTH/BTC</li>
              <li>GGB/BTC</li>
            </ul>
          </div>
          <div class="price">
            <span class="rise">0.00022005</span>
            <span>8.04</span>
          </div>
          <div class="rise_fall rise">
            <span>0.00000357</span>
            <span>+1.50%</span>
          </div>
          <div class="price_detail">
            <div>
              <span>24h</span>
              <span>0.00024480</span>
            </div>
            <div>
              <span>24h</span>
              <span>0.00020601</span>
            </div>
            <div>
              <span>24h</span>
              <span>578.79 BTC</span>
            </div>
          </div>
        </div>
        <div class="k_line">K</div>
        <div class="deal_record">
          <div class="title"></div>
          <table>
            <tr>
              <th></th>
              <th></th>
              <th></th>
            </tr>
            <tr>
              <td class="fall">0.00021637</td>
              <td>144</td>
              <td>09:30:22</td>
            </tr>
            <tr>
              <td class="rise">0.00021637</td>
              <td>20</td>
              <td>09:11:15</td>
            </tr>
          </table>
        </div>
      </div>
      <div class="foot">
        <div class="buy"></div>
        <div class="sell"></div>
      </div>
    </div>
    <transition name="slide" mode="out-in"><router-view class="info-contariner"></router-view></transition>
  </div>
</template>

<script>
import appHead from '../../commons/appHead'
export default {
  name: 'conjuncture',
  data () {
    return {
      Msg: '',
      rotate: true
    }
  },
  components: {
    appHead
  },
  mounted: function () {
    this.init()
  },
  methods: {
    init () {
      let self = this;
      $(document).ready(() => {
        $('.tag').click(function () {
          $(this).addClass('active').siblings().removeClass('active')
        });
        $('.coin_type').click(function () {
           $('.tab').slideToggle('fast');
          if(self.rotate){
            $('.rotate').css('transform', 'rotate(180deg)');
            self.rotate = false;
          }else{
            $('.rotate').css('transform', 'rotate(0deg)');
            self.rotate = true;
          }
        });
        $('.tab li').click(function(){
          let val = $(this).html();
          $('.coin_type span').html(val)
        })
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  @import "../../assets/style/common.css";
  @mixin flex {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  @mixin font($size, $color: white) {
    font-size: $size;
    font-family: "";
    color: $color;
  }
  .conjuncture {
    position: absolute;
    left: 0;
    top: 0;
    overflow: hidden;
    width: 100%;
    height: 100%;
    background-color: white;
    .wrap {
      margin-bottom: 2rem;
      height: 100%;
      overflow-y: auto;
    }
    .market {
      @include flex;
      margin-top: 2.05rem;
      font-family: "";
      width: 100%;
      height: 1.9rem;
      background-color: #f8f8f8;
      & > div {
        @include flex;
        flex: 1;
        height: 100%;
        font-size: 0.76rem;
        color: #676767;
        &.active {
          color: #ffb35a;
          border-top: 0.15rem solid #ffb35a;
          /*background: white;*/
        }
      }
    }
    .conjuncture_detail {
      margin-bottom: 3rem;
      @include font(0.8rem,#676767);
      .data_wrap {
        @include flex;
        flex-direction: column;
        & > div {
          position: relative;
          margin: 0.1rem 0;
          width: 100%;
        }
        .rise_fall {
          font-size: 0.6rem;
        }
        .rise {
          color: #19b369;
        }
        .coin_type {
          img {
            width: 0.73rem;
            transition: all .3s;
          }
          ul {
            display: none;
            position: absolute;
            top: 1.2rem;
            margin: 0;
            padding-left: 0;
            z-index: 666;
            width: 100%;
            list-style-type: none;
            background-color: white;
              li {
                padding: 0.2rem;
                border-bottom: 1px solid gray;
                &:last-child {
                  border: none;
                }
              }
          }
        }
        .price_detail {
          @include flex;
          @include font(0.7rem,#a6a6b0);
          margin-top: 0.5rem;
          width: 90%;
          flex-direction: row;
          & > div {
            & > span {
              &:last-child {
                color: #676767;
              }
            }
          }
        }
      }
      .k_line {
        @include flex;
        margin: 0.5rem 0;
        width: 100%;
        height: 11rem;
        color: red;
        background-color: black;
      }
      .deal_record {
        margin: 0 auto;
        width: 90%;
        .title {
          @include font(0.7rem,#676767);
          text-align: left;
          padding: 0.3rem 0;
          border-bottom: 1px solid #d2d2d7;
        }
        table {
          @include font(0.7rem,#a6a6b0);
          width: 100%;
          & td {
            color: #676767;
            &:first-child {
              padding-right: 2rem;
              text-align: left
            }
            &:nth-of-type(2) {
              padding-right: 1rem
            }
            &.rise {
              color: #1bb368;
            }
            &.fall {
              color: red;
            }
          }
          & th {
            &:first-child {
              padding-right: 3.4rem;
            }
            &:nth-of-type(2) {
             padding-right: 1rem;
            }
          }
        }
      }
    }
    .foot {
      position: fixed;
      bottom: 0;
      display: flex;
      width: 100%;
      height: 2.4rem;
      & > div {
        flex: 1;
        @include flex;
        height: 100%;
        color: white;
        &.buy {
          background-color: #1bb368;
        }
        &.sell {
          background-color: #ff0000;
        }
      }
    }
  }

</style>



