<template>
  <div class="app_head">
    <!---->
    <section class="title_back" v-if="back" @click="$router.go(-1)">
      <img src="../assets/image/close.png" alt="">
    </section>
    <!---->
      <section class="title_mid" v-if="mid" :style="{color: color}">
          <span class="mid_text">{{mid}}</span>
      </section>
    <!---->
      <section class="title_right" v-if="right" >
          <span class="right_text">
              <img src="../assets/image/hamburger.png" alt="">
          </span>
        <ul class="menu">
          <li @click="$router.push('/homePage')"></li>
          <li @click="$router.push('/conjuncture')"></li>
          <li @click="$router.push('/userCenter')"></li>
        </ul>
      </section>
    <!---->
    <section class="title_right" v-if="add">
          <span class="right_text">
              <img src="../assets/image/add.png" alt="">
          </span>
    </section>
    <section class="title_right" v-if="text">
          <span class="text">{{text}}</span>
    </section>
  </div>
</template>

<script>
export default {
  name: 'appHead',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  props: ['back', 'mid', 'right', 'add', 'text', 'color'],
  mounted: function(){
      this.init();
  },
  methods: {
      init(){
          $(document).ready(() => {
              $('.title_right').click(function () {
                  $('.menu').slideToggle('fast');
              })
          })
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!-- 1rem = 24px -->
<style scoped lang="scss">
$height : 45px;
$bg : #222222;
@mixin height {
  height: 2rem;
  line-height: 2rem;
}
@mixin flex {
  display: flex;
  justify-content: center;
  align-items: center;
}
@mixin img {
  width: 60%;
  height: 60%;
}
@mixin font($size, $color: white) {
  font-size: $size;
  font-family: "";
  color: $color;
}
.app_head {
  position: fixed;
  top: 0;
  z-index: 999;
  text-align: center;
  font-family: "";
  width: 100%;
  @include height;
  color: white;
  background-color: $bg;
  ul {
    list-style-type: none;
    padding: 0;
    li {
      display: inline-block;
    }
  }
  a {
    text-decoration: none;
    &:hover { color: #ffb3ff };
  }
  .title_back {
    position: absolute;
    top: 0.3rem;
    left: 0;
    width: 2rem;
    height: 2rem;
    & > img {
      @include img;
    }
  }
  .title_mid {
    span {
      font-size: 0.9rem;
    }
  }
  .title_right {
    position: absolute;
    top: 0;
    right: 5px;
    .right_text {
      @include flex;
      width: 2rem;
      height: 2rem;
      img {
        @include img;
      }
    }
    .text {
      @include flex;
      margin-right: 0.2rem;
    }
    .menu {
      display: none;
      margin: 0;
      padding: 0;
      position: absolute;
      top: 2rem;
      left: -2.7rem;
      width: 5rem;
      background-color: white;
      @include font(0.7rem,#444444);
      li {
        width: 100%;
      }
    }
  }
}



</style>



